export default {
  add (...args) {
    let result = 0
    args.forEach(arg => {
      result += arg
    })
    return result
  },
  sub (...args) {
    let result
    args.forEach((arg, idx) => {
      if (idx === 0) {
        result = arg
      } else {
        result -= arg
      }
    })
    return result
  }
}



